export default {
    props: {
        Jurisdiction: {
            type: Object,
            default: {},
            required: true
        },
        list: {
            type: Array,
            default: [{
                img: '/static/img/global/vip-manage.png',
                title: "会员管理",
                url: ''
            }, {
                img: '/static/img/global/self-paybuy.png',
                title: "自提核销",
                url: ''
            }, {
                img: '/static/img/global/calcu-data.png',
                title: "数据统计",
                url: ''
            }]
        }
    },
    computed: {
        showApps() {
            return this.list.filter((item, index) => {
                if (index == 2) {
                    return this.Jurisdiction['statistics_index_view']; //数据概览权限
                } else if (index == 0) {
                    return this.Jurisdiction['member_list_view']; //用户列表权限
                } else if (index == 1) {
                    return this.Jurisdiction['apps_index_verify']; //核销小程序权限
                }
                return true;
            })
        }
    },
    methods: {
        click(item) {
            this.$emit('click', item)
        }
    },
}
